/* MAIN */

/*html,
body {
    height: 100%;
}*/

.no-scroll {
    height: 100%;
    overflow: hidden;
}

@media screen and (min-width: 768px) {

    .no-scroll {
        overflow: auto;
    }

}

@media screen and (min-width: 768px) {

    .hidden-md-up {
        display: none;
    }

}

body {
    background: #fafafa;
    font-family: 'Open Sans', sans-serif;
}

#root {
    height: 100%;
}

.main-container {
    max-width: 970px;
    margin: auto;
}

/* MAIN MENU */

.col-menu {
    display: none;
    height: 190px;
    /* border: 1px solid black; */
    padding: 0;
}

@media screen and (min-width: 768px) {
    
    .col-menu {
        display: block;
    }
    
}

/* BUTTONS AND LINKS */

.button-no-style {
    outline: none !important;
    background: none !important;
    border: none;
    -webkit-box-shadow: none !important;
            box-shadow: none !important;
    
}

.button-tools {
    padding: 0;
    color: #ddd;
    font-size: 1.2em;
}

.link-no-style {
    text-decoration: none !important;
    outline: none !important;
    color: #333;
}

/* DROPDOWN MENU */

.dropdown-menu {
    background: none;
    -webkit-box-shadow: none;
            box-shadow: none;
    border: none;
    z-index: 1000;
}

.dropdown-menu li {
    padding: 4px;
    margin: 2px 0;
    border: 1px solid #ddd;
    border-radius: 5px;
    background: #fff;
    /*font-size: 1.em;*/
}

.dropdown-menu li:hover {
    border: 1px solid rgb(249, 73, 182);
}

.dropdown-menu li a {
    background: #fff !important;
    border: none !important;
    outline: none !important;
}

/* FORMS */

.input-round input[type="text"],
.input-round input[type="password"],
.input-round textarea {
    border: 1px solid #ddd !important;
    outline: none !important;
    -webkit-box-shadow: 0 0 0 30px rgb(255, 255, 255) inset !important;
            box-shadow: 0 0 0 30px rgb(255, 255, 255) inset !important;
    font-size: 0.95em;
}

.input-round input[type="text"],
.input-round input[type="submit"],
.input-round input[type="password"] {
    padding-left: 20px;
    border-radius: 30px;
}

.input-round input[type="text"]:focus,
.input-round input[type="password"]:focus,
.input-round textarea:focus {
    border: 1px solid rgb(249, 73, 182) !important;
}   

.input-round .glyphicon {
    color: rgb(249, 73, 182) !important;
}

.icon-loader svg {
    width: 40px;
    height: 40px;
    fill: #f949b6;
}

/*POST ITEM */

.post-item {
    position: relative;
    min-height: 81px;
    word-wrap: break-word;
    opacity: 1;
    -webkit-transition: opacity 0.4s linear;
    -o-transition: opacity 0.4s linear;
    transition: opacity 0.4s linear;
}

.post-item-deleted {
    opacity: 0.5;
}

.post-item .post-content {
    cursor: pointer;
}

.dropdown-menu {
    border-radius: 0;
}

.post-text {
    color: #333;
}

.post-time {
    color: #888;
    font-size: 0.8em;
}

.post-option .glyphicon {
    vertical-align: middle;
}

/* MODALS */

.modal-dialog {
    height: 100%;
    width: 100%;
    margin: 0;
}

.modal-content {
    border: 0;
    border-radius: 0;
    height: 100%;
    width: 100%;
}

@media screen and (min-width: 768px) {

    .modal-dialog {
        margin: auto;
        margin-top: calc(50vh - 300px);
        height: 600px;
        width: 600px;
    }

    .modal-content {
        width: 600px;
        /*margin-left: calc(50vw - 300px); */
    }

}

.modal-header {
    text-align: center;
    height: 50px;
    font-size: 1.2em;
}

.modal-header .title-icon {
    width: 20px;
    height: 20px;
    vertical-align: top;
    margin: 0 15px;
}

.modal-header .title-text {
    font-size: 1em;
    line-height: 22px;
    vertical-align: top;
}

.modal-header .close {
    margin: 0;
}

.modal .modal-body {
    padding: 0;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: justify;
        justify-content: space-between;
    height: calc(100% - 50px);
}

.modal .post-composer {
    width: 100%;
    border: 1px solid #ccc;
    background: #fff;
    /*z-index: 2;*/
    -ms-flex-negative: 0;
        flex-shrink: 0;
}

.post-composer-main .textarea {
    max-height: 30vh;
    padding: 8px 16px;
}


/* AVATAR */

.avatar {
    display: inline-block;
    border-radius: 50%;
    width: 40px;
    height: 40px;
    /*border: 1px solid #888;*/
    overflow: hidden;
}

.avatar img {
    width: 100%;
}

/* FORMS */

.input {
    outline: none;
    border: 1px solid #ddd;
    -webkit-box-shadow: none;
            box-shadow: none;
    border-radius: 30px;
    padding: 8px 24px;
}

.input:focus {
    border: 1px solid rgb(249, 73, 182);
}

.input.input-warning {
    border: 1px solid #FF9800;
}

.input.input-error {
    border: 1px solid #F44336;
}

.input.input-success {
    border: 1px solid #4CAF50;
}

.input-help {
    width: 100%;
    text-align: left;
    display: inline-block;
    padding: 0 10px;
    margin: 10px 0;
    /*display: none;*/
}


/* FIXED BUTTONS */

.fixed-buttons-area {
    position: fixed;
    z-index: 1000;
    bottom: calc(1% + 50px);
    right: 5%;
}

.fixed-buttons-area button {
    display: block;
    text-align: center;
    margin: 10px 0;
}

.fixed-buttons-area svg {
    width: 40px;
    height: 40px;
}

.fixed-buttons-area button.new-offer-button {
    /*
        display: none не работает на мобльном
        пропадает иконка в меню
    */
    height: 0;
    overflow: hidden;
    visibility: hidden;
}

@media screen and (min-width: 768px) {

    .fixed-buttons-area svg {
        width: 50px;
        height: 50px;
    }

    .fixed-buttons-area button.new-offer-button {
        height: auto;
        visibility: visible;
    }

}

/* SCROLL BODY UP */

.button-scroll-body-up {
    visibility: visible;
    opacity: 1;
    -webkit-transition: all .5s linear;
    -o-transition: all .5s linear;
    transition: all .5s linear;
}

@media screen and (min-width: 768px) {
    .button-scroll-body-up {
        bottom: 5%;
        right: 3%;
    }
}

.button-scroll-body-up--hidden {
    visibility: hidden;
    opacity: 0;
}

.button-scroll-body-up svg {
    width: 20;
    height: 20;
    fill: #f949b6;
}

.unreaded-posts {
    padding: 2px 8px;
    /*border: 1px solid #dadada;*/
    background: #fff;
    color: #f949b6;
    border-radius: 50%;
}

/* IMAGES */

.icon {
    height: 18px;
}

svg.icon-mars {
    fill: #3A9ED1;
}

svg.icon-venus {
    fill: #f378ac;
}


/* HASHTAG */

.hashtag {
    color: #01579B;
    padding: 0 2px;
}


.hashtag:before {
    content: '#';
}



.avatar-cropper .cropper-view-box,
.avatar-cropper .cropper-face {
    border-radius: 50%;
}


.fade-appear,
.fade-enter {
    opacity: 0;
    width: 100%;
    position: absolute;
    z-index: 2;
}

.fade-appear.fade-appear-active,
.fade-enter.fade-enter-active {
    opacity: 1;
    -webkit-transition: opacity .7s ease-out;
    -o-transition: opacity .7s ease-out;
    transition: opacity .7s ease-out;
}

.fade-leave {
    opacity: 1;
    width: 100%;
    display: inline-block;
    position: absolute;
}

.fade-leave.fade-leave-active {
    opacity: 0;
    -webkit-transition: opacity .7s ease-out;
    -o-transition: opacity .7s ease-out;
    transition: opacity .7s ease-out;
}


